@import "./helpers";

:root {
	scroll-behavior: smooth;
	font-size: 16px;
	@include lg-screen {
		font-size: 14px;
	}
	@include md-screen {
		font-size: 12px;
	}
	@include sm-screen {
		font-size: 10px;
	}
}

* {
	box-sizing: border-box;
}

body {
	position: relative;
	margin: 0;
	font-family: "Spline Sans Mono", monospace;
}

::-webkit-scrollbar {
	width: 5px;
}

::-webkit-scrollbar-track {
	background-color: rgb(225, 225, 225);
}

::-webkit-scrollbar-thumb {
	background-image: linear-gradient(
		to bottom,
		#2669bf,
		#625fba,
		#8953ae,
		#a6449b,
		#bb3683
	);
	border-radius: 10px;
}

.gradiant-btn {
	background-image: $gradiant;
}

a {
	color: inherit;
	text-decoration: none;
}
