@import "../../styles/helpers";

.guide {
	position: relative;
	padding: 50px 0;
	min-height: 100vh;
	:global {
		.header {
			width: fit-content;
			margin: 0 auto 80px;
			.border {
				width: 50%;
				margin: auto;
				height: 1px;
				background-color: rgba(0, 0, 0, 0.246);
			}
		}
		.MuiPaper-root {
			.border {
				height: 3px;
				background-image: $gradiant;
			}
			.MuiCardContent-root {
				height: 300px;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				align-items: center;
				background-color: rgb(243, 243, 243);
				@include sm-screen {
					height: 200px;
				}
			}
		}
	}
}
