@import "../../styles/helpers";

.cover {
	position: relative;
	height: 100vh;
	overflow: hidden;
	:global {
		.overlay {
			position: absolute;
			top: 0;
			width: 100%;
			height: 100%;
			background-image: linear-gradient(180deg, transparent, #000000ab);
		}
		.video {
			width: 100%;
			@include md-screen {
				width: inherit;
				height: 100%;
			}
		}
		.content {
			position: absolute;
			z-index: 1000;
			top: 50%;
			left: 50%;
			width: 80%;
			transform: translate(-50%, -50%);
			color: white;
			.text {
				margin-bottom: 50px;
			}
			.actions {
				text-align: center;
				.MuiButtonBase-root {
					width: 150px;
					padding: 10px;
					margin: 5px 10px;
					&.MuiButton-outlined {
						color: white;
						border-color: white;
					}
				}
			}
		}
	}
}

.scrollBtn {
	position: absolute !important;
	bottom: 20px;
	left: 48%;
	animation: bounceUpDown 0.8s alternate linear infinite;
}

.docsModal {
	display: flex;
	justify-content: center;
	align-items: center;
}

@keyframes bounceUpDown {
	0% {
		transform: translateY(0px);
	}
	100% {
		transform: translateY(5px);
	}
}
