@import "../../styles/helpers";

.docs {
	position: relative;
	display: flex;
	width: 60%;
	height: 80%;
	border-radius: 10px;
	background-color: rgb(255, 255, 255);
	@include md-screen {
		width: 90%;
		height: 80%;
	}
	:global {
		.MuiIconButton-root {
			position: absolute;
			left: 50%;
			top: -40px;
			width: 35px;
			height: 35px;
			transform: translateX(-50%);
			background-color: white;
			&:hover {
				background-color: inherit;
			}
			@include sm-screen {
				top: -30px;
				width: 25px;
				height: 25px;
			}
		}
		.MuiTabs-root {
			width: 20%;
			padding: 10px 0;
			border-right: 1px solid rgb(230, 230, 230);
			.MuiTab-root {
				text-transform: capitalize;
			}
			@include sm-screen {
				width: 35%;
				.MuiTab-root {
					padding: 10px;
				}
			}
		}
		.content {
			padding: 20px;
			flex: 1;
			white-space: pre-line;
			overflow: auto;
			@include sm-screen {
				padding: 10px;
			}
		}
	}
}
