@import "../../styles/helpers";

.article {
	height: 100%;
	display: flex;
	flex-direction: column;
	:global {
		.MuiCardMedia-root {
			height: 200px;
		}
		.MuiCardContent-root {
			flex: 1;
		}
		.MuiCardActions-root {
			justify-content: space-between;
		}
		.MuiButtonBase-root {
			text-transform: capitalize;
		}
		.border {
			height: 5px;
			background-image: $gradiant;
		}
	}
}
