@import url("https://fonts.googleapis.com/css2?family=Spline+Sans+Mono:ital,wght@0,400;0,600;1,400&display=swap");

//========== MIXINS ==========//

@mixin sm-screen {
	@media (max-width: 600px) {
		@content;
	}
}

@mixin md-screen {
	@media (max-width: 800px) {
		@content;
	}
}

@mixin lg-screen {
	@media (max-width: 1200px) {
		@content;
	}
}

//========== VARIABLES ==========//

$gradiant: linear-gradient(
	to right,
	#2669bf,
	#625fba,
	#8953ae,
	#a6449b,
	#bb3683
);

