.navbar {
	position: absolute;
	z-index: 100;
	left: 10%;
	width: 80%;
	padding: 10px 0;
	display: flex;
	justify-content: space-between;
	align-items: center;
	color: white;
	border-bottom: 1px solid rgba(255, 255, 255, 0.58);
	:global {
		.logo {
			width: 12vw;
			min-width: 75px;
			display: block;
		}
	}
}
